    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				txwaylist:[],
				current:0
			}				
		},
		computed: mapState(['sellerHasLogin']),
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
		onShow(){
			 if (!this.sellerHasLogin) {  
				uni.navigateTo({
					url:'../login',
				})
			}
		},
        methods: {  
            async loadData() {  
				var _this = this;
                if (!this.sellerHasLogin) {  
					uni.navigateTo({
						url:'../login',
					})
                }
				
				_this.$Request.post('seller_fund/txwaylist').then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.txwaylist = res.datas.list;
					}
				}).catch(function (err) {
					console.log(err);
				  });
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			checkTxWay(id){
				for (let i = 0; i < this.txwaylist.length; i++) {
					if (this.txwaylist[i].id === id) {
						this.current = id;
						this.txwaylist[i].is_default=0;
						//break;
					}
					this.txwaylist[i].is_default=0;
				}
				this.$Request.post('seller_fund/isdefaut',{id:id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						uni.navigateTo({
							url:"tx", 
							success (res) { 
								let page = getCurrentPages().pop(); 
								if (page == undefined || page == null) { 
								  return ;
								 } 
								 page.onLoad() ;
							}
						})
						
					}
				});
			}
			 
        }  
    }  
